<template>
    <div>
        <div>
            <el-input
                    placeholder="添加职称..."
                    prefix-icon="el-icon-plus"
                    size="small"
                    style="width: 300px;"
                    v-model="jl.name">
            </el-input>
            <el-select v-model="jl.titleLevel" placeholder="职称等级" size="small" style="margin: 0px 8px 0px 8px;">
                <el-option
                        v-for="item in titleLevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button
                    icon="el-icon-plus"
                    type="primary"
                    @click="addJobLevel"
                    size="small">
            添加职称</el-button>
        </div>
        <div style="margin-top: 10px;">
            <el-table
                    :data="jobLevels"
                    border
                    size="small"
                    stripe
                    @selection-change="handleSelectionChange"
                    style="width: 80%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="70">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职称名称"
                        width="170">
                </el-table-column>
                <el-table-column
                        prop="titleLevel"
                        width="170"
                        label="职称级别">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        width="200"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        width="120"
                        label="是否启用">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditWindow(scope.row)">编辑</el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" size="samll" style="margin-top: 10px" :disabled="multipleSelection.length == 0" @click="batchDelete">批量删除</el-button>
            <el-dialog title="修改职称" :visible.sync="dialogVisible">
                <div id="editJobDiv">
                    <div>
                        <el-tag size="small" style="margin-left: 10px;">
                            职称名
                        </el-tag>
                        <el-input type="text" size="small" v-model="updateJl.name" style="width: 200px; margin-left: 22px;"></el-input>
                    </div>
                    <div>
                        <el-tag size="small">职称级别</el-tag>
                        <el-select v-model="updateJl.titleLevel" placeholder="职称等级" size="small">
                            <el-option
                                    v-for="item in titleLevels"
                                    :key="item"
                                    :label="item"
                                    :value="item">
                            </el-option>
                        </el-select>
                    </div>
                    <div>
                        <el-tag size="small">是否启用</el-tag>
                        <el-switch
                                style="margin-left: 20px;"
                                v-model="updateJl.enabled"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="启用"
                                inactive-text="禁用">
                        </el-switch>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="doUpdateJob">确 定</el-button>
                </span>
            </el-dialog>
        </div>
    </div>

</template>

<script>
    export default {
        name: "JobLevelMana",
        data() {
            return {
                updateJl: {
                    name: '',
                    titleLevel: '',
                    enabled: false
                },
                multipleSelection: [],
                dialogVisible: false,
                jl: {
                    name: '',
                    titleLevel: '',
                    enabled: false
                },
                titleLevels: [
                    "正高级",
                    "副高级",
                    "中级",
                    "初级",
                    "员级"
                ],
                jobLevels: []
            }
        },
        mounted() {
            this.initJobLevel();
        },
        methods: {
            initJobLevel() {
                this.getRequest("/system/basic/joblevel/").then(resp => {
                   if (resp) {
                       this.jobLevels = resp;
                   }
                });
            },
            addJobLevel() {
                if (this.jl.name && this.jl.titleLevel) {
                    this.postRequest("/system/basic/joblevel/", this.jl).then(resp => {
                        if (resp) {
                            this.initJobLevel();
                            this.jl = {
                                name: '',
                                titleLevel: ''
                            }
                        }
                    });
                } else {
                    this.$message.error("添加字段不能为空!");
                }
            },
            handleDelete(data) {
                this.$confirm('此操作将永久删除【'+ data.name +'】该职称, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/joblevel/" + data.id).then(resp => {
                        if (resp) {
                            this.initJobLevel();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            showEditWindow(data) {
                Object.assign(this.updateJl, data);
                this.dialogVisible = true;
            },
            doUpdateJob() {
                this.putRequest("/system/basic/joblevel/", this.updateJl).then(resp => {
                    if (resp) {
                        this.initJobLevel();
                        this.dialogVisible = false;
                    }
                });
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            batchDelete() {
                this.$confirm('此操作将永久删除【'+ this.multipleSelection.length +'】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = "?ids=";
                    this.multipleSelection.forEach(job => {
                        ids += job.id + "&ids=";
                    });

                    ids = ids.substring(0, ids.lastIndexOf("&"));
                    this.deleteRequest("/system/basic/joblevel/" + ids).then(resp => {
                       if (resp) {
                           this.initJobLevel();
                       }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        }
    }
</script>

<style>
    #editJobDiv div{
        display: flex;
        align-items: flex-start;
        margin: 0px 0px 10px 10px;
    }
</style>