<template>
    <div>
        <div class="perManaTop">
            <el-input placeholder="请输入角色英文名" v-model="role.name" size="small">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input placeholder="请输入角色中文名" size="small" v-model="role.nameZh" @keydown.enter.native="addRole"></el-input>
            <el-button size="small" type="primary" icon="el-icon-plus" @click="addRole">添加角色</el-button>
        </div>
        <div class="perManaMain">
            <el-collapse accordion @change="showMenu" v-model="activeNames">
                <el-collapse-item :title="role.nameZh" :name="role.id" v-for="(role, index) in roles" :key="index">
                    <div>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span>可访问的资源</span>
                                <el-button style="float: right; padding: 3px 0; color: #ff0000" type="text" icon="el-icon-delete" @click="deleteRole(role)"></el-button>
                            </div>
                            <div>
                                <el-tree
                                        :data="allMenu"
                                        :props="props"
                                        node-key="id"
                                        ref="tree"
                                        :default-checked-keys="selectedMenus"
                                        show-checkbox>
                                </el-tree>
                            </div>
                            <div style="display: flex; justify-content: flex-end">
                                <el-button size="small" @click="cancelUpdate">取消修改</el-button>
                                <el-button size="small" type="primary" @click="doUpdateMenuRole(role.id, index)">确认修改</el-button>
                            </div>
                        </el-card>
                    </div>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PermissMana",
        data() {
            return {
                role: {
                    name: '',
                    nameZh: ''
                },
                activeNames: -1,
                roles: [],
                allMenu: [],
                selectedMenus: [],
                props: {
                    children: 'children',
                    label: 'name'
                }
            }
        },
        mounted() {
          this.initRoles();
        },
        methods: {
            addRole() {
                if (this.role.name && this.role.nameZh) {
                    this.postRequest("/system/basic/permiss/", this.role).then(resp => {
                       if (resp) {
                           this.initRoles();
                           this.role.name = "";
                           this.role.nameZh = "";
                       }
                    });
                } else {
                    this.$message.error("数据不能为空");
                }
            },
            deleteRole(role) {
                this.$confirm('此操作将永久删除【'+ role.nameZh +'】角色, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/permiss/" + role.id).then(resp => {
                        if (resp) {
                            this.initRoles();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            initRoles() {
                this.getRequest("/system/basic/permiss/").then(resp => {
                    if (resp) {
                        this.roles = resp;
                    }
                });
            },
            initSelectedMenus(rid) {
                this.getRequest("/system/basic/permiss/menus/" + rid).then(resp => {
                    if (resp) {
                        this.selectedMenus = resp;
                    }
                });
            },
            showMenu(rid) {
                if (rid) {
                    this.initSelectedMenus(rid);
                    this.initAllMenu();
                }
            },
            initAllMenu() {
                this.getRequest("/system/basic/permiss/menus").then(resp => {
                   if (resp) {
                       this.allMenu = resp;
                   }
                });
            },
            cancelUpdate() {
                this.activeNames = -1;
            },
            // index表示当前面板展开的角色的索引
            doUpdateMenuRole(rid, index) {
                let tree = this.$refs.tree[index];
                // 将参数设置为true时，只显示被选中叶子节点的key
                let selectedKeys = tree.getCheckedKeys(true);
                let url = "/system/basic/permiss/?rid=" + rid;
                selectedKeys.forEach(key => {
                   url += "&mids=" + key;
                });

                this.putRequest(url).then(resp => {
                   if (resp) {
                       // 将面板选中设置为-1，即关闭当前面板
                       this.activeNames = -1;
                   }
                });
            }
        }
    }
</script>

<style>
    .perManaTop {
        display: flex;
        justify-content: flex-start;
    }

    .perManaTop .el-input{
        width: 300px;
        margin-right: 7px;
    }

    .perManaMain {
        width: 700px;
        margin-top: 10px;
    }
</style>