<template>
    <div style="width: 500px;">
        <el-input
                placeholder="请输入查找的部门名称..."
                v-model="filterText">
        </el-input>

        <!-- 当输入框的值发生变化时，则调用filterNode该方法 -->
        <el-tree
                class="filter-tree"
                :data="deps"
                :props="defaultProps"
                :expand-on-click-node="false"
                :filter-node-method="filterNode"
                ref="tree">
            <span class="custom-tree-node btnSlot" slot-scope="{ node, data }">
            <span>{{ node.label }}</span>
            <span>
              <el-button
                      class="deptBen"
                      type="primary"
                      size="mini"
                      @click="() => showAddDeptView(data)">
                添加部门
              </el-button>
              <el-button
                      class="deptBen"
                      type="danger"
                      size="mini"
                      @click="() => deleteDept(data)">
                删除部门
              </el-button>
            </span>
          </span>
        </el-tree>
        <el-dialog
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <table>
                    <tr>
                        <td><el-tag>上级部门</el-tag></td>
                        <td>{{depName}}</td>
                    </tr>
                    <tr>
                        <td><el-tag>部门名称</el-tag></td>
                        <td><el-input size="small" placeholder="请输入添加的部门名称..." type="text" v-model="dep.name"></el-input></td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addDep">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DeptMana",
        data() {
            return{
                deps: [],
                filterText: '',
                dialogVisible: false,
                dep: {
                    name: '',
                    parentId: -1
                },
                depName: '',
                defaultProps: {
                    children: 'children',
                    label: 'name'
                }
            }
        },
        /**
         * 监听filterText属性的变化，val即当前最新值
         */
        watch: {
            filterText(val) {
                // 获取树形控件节点元素，调用所绑定的方法
                this.$refs.tree.filter(val);
            }
        },
        mounted() {
            this.initDeps();
        },
        methods: {
            /**
             *  如果value值不存在，则返回true，表示保留当前所有数据
             *  如果value存在，则在数据中查找value是否有匹配的值，如果有，则返回数据, 否则表示未找到
             * @param value   表示当前所查找的值
             * @param data    表示查找的数据
             * @returns {boolean}
             */
            filterNode(value, data) {
                if (!value) return true;
                return data.name.indexOf(value) !== -1;
            },
            initDeps() {
                this.getRequest("/system/basic/department/").then(resp => {
                   if (resp) {
                       this.deps = resp;
                   }
                });
            },
            showAddDeptView(data) {
                this.depName = data.name;
                this.dep.parentId = data.id;
                this.dialogVisible = true;
            },
            removeDepFromDeps(p, deps, id) {
                for (let i=0; i<deps.length; i++) {
                    let d = deps[i];
                    /*如果删除的部门id等于当前部门id*/
                    if (d.id == id) {
                        deps.splice(i, 1);
                        // 删除完成后判断它的父部门下是否还存在子部门
                        if (p.children.length == 0) {
                            // 将parent属性更改
                            p.parent = false;
                        }
                        return;
                    } else {
                        // 继续遍历子部门查找
                        this.removeDepFromDeps(d, d.children, id);
                    }
                }
            },
            deleteDept(data) {
                if (data.parent) {
                    this.$message.error("该部门下存在子部门，无法删除");
                } else {
                    this.$confirm('此操作将永久删除【'+ data.name +'】该部门, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequest("/system/basic/department/" + data.id).then(resp => {
                           if (resp) {
                               this.removeDepFromDeps(null, this.deps, data.id);
                           }
                        });
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }
            },
            resetDep() {
                this.dep =  {
                    name: '',
                    parentId: -1
                };
                this.depName = '';
            },
            addDep2Deps(deps, dep) {
                for (let i=0; i<deps.length; i++) {
                    let d = deps[i];
                    /*如果当前添加的部门的父id等于部门节点中的id*/
                    if (d.id == dep.parentId) {
                        /*把添加的部门节点插入到它父节点的子元素中*/
                        d.children = d.children.concat(dep);
                        // 添加完成后判断父部门下是否存在子部门
                        if (d.children.length > 0) {
                            // 更改parent属性
                            d.parent = true;
                        }
                        return;
                    } else{
                        this.addDep2Deps(d.children, dep);
                    }
                }
            },
            addDep() {
                this.postRequest("/system/basic/department/", this.dep).then(resp => {
                    if (resp) {
                        this.addDep2Deps(this.deps, resp.object);
                        this.dialogVisible = false;
                        /*重置参数*/
                        this.resetDep();
                    }
                });
            }
        },

    }
</script>

<style>
    .deptBen {
        padding: 2px;
    }

    .btnSlot {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
</style>