<template>
    <div>
        <div style="margin-top: 10px; display: flex; justify-content: center">
            <el-input
                    v-model="keywords"
                    placeholder="通过用户名搜索用户..."
                    @keydown.enter.native="doSearch"
                    style="width: 400px; margin-right: 10px;"
                    prefix-icon="el-icon-search">
            </el-input>
            <el-button icon="el-icon-search" type="primary" @click="doSearch">搜索</el-button>
        </div>
        <div class="hr-container">
            <el-card class="box-card hr-Card" v-for="(hr, index) in hrs" :key="index">
                <div slot="header" class="clearfix">
                    <span>{{hr.name}}</span>
                    <el-button style="float: right; padding: 3px 0; color: #ff0000" type="text" icon="el-icon-delete" size="small"></el-button>
                </div>
               <div>
                   <div class="img-container">
                       <img :src="hr.userface" :alt="hr.name" :title="hr.name" class="userface-img">
                   </div>
                   <div class="userinfo-container">
                       <div>用户名: {{hr.name}}</div>
                       <div>手机号码: {{hr.phone}}</div>
                       <div>电话号码: {{hr.telephone}}</div>
                       <div>地址: {{hr.address}}</div>
                       <div>用户状态: <el-switch
                               @change="changeEnabled(hr)"
                               style="margin-left: 5px;"
                               v-model="hr.enabled"
                               active-color="#13ce66"
                               inactive-color="#ff4949"
                               active-text="启用"
                               inactive-text="禁用">
                       </el-switch></div>
                       <div style="margin-top: 2px;">用户角色:
                           <el-tag type="success"
                                   v-for="(role, indexj) in hr.roles"
                                   style="margin-left: 5px;"
                                   size="mini"
                                   :key="indexj">
                               {{role.nameZh}}
                           </el-tag>
                           <el-popover
                                   placement="right"
                                   title="角色列表"
                                   @show="showPop(hr)"
                                   @hide="hidePop(hr)"
                                   width="270"
                                   trigger="click">
                               <el-select v-model="selectedRoles" multiple placeholder="请选择">
                                   <el-option
                                           v-for="(r, indexk) in allRoles"
                                           :key="indexk"
                                           :label="r.nameZh"
                                           :value="r.id">
                                   </el-option>
                               </el-select>
                               <el-button slot="reference" size="mini" type="text" icon="el-icon-more" style="margin-left: 3px;"></el-button>
                           </el-popover>
                       </div>
                   </div>
               </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SysHr",
        data() {
            return {
                keywords: '',
                hrs: [],
                allRoles: [],
                selectedRoles: []
            }
        },
        mounted() {
          this.initHrs();
        },
        methods: {
            doSearch() {
                this.initHrs();
            },
            // 用户进行角色更新操作
            hidePop(hr) {
                let url = "/system/hr/role?hrid=" + hr.id;
                this.selectedRoles.forEach(sr=>{
                   url += '&rids=' + sr;
                });

                let roles = [];
                // 将hr.roles拷贝一份，防止在遍历数据时而导致出现空数据
                Object.assign(roles, hr.roles);
                let flag = false;
                if (roles.length != this.selectedRoles.length) {
                    flag = true;
                } else {
                    for (let i = 0; i < roles.length; i++) {
                        let role = roles[i];
                        for (let j = 0; j < this.selectedRoles.length; j++) {
                            let sr = this.selectedRoles[j];
                            // 用户拥有的角色存在于预选中数组中时
                            if (role.id == sr) {
                                // 将所存在的这个角色从数组中移除
                                roles.splice(i, 1);
                                /**
                                 *  当将存在的角色从数组中移除后
                                 *  数组的下一个元素会被放置到上一个元素的位置，即删除元素的位置
                                 *  如i为2的元素被删除，则i为3的元素会在i为2的位置上，这时候i++
                                 *  i的值为3，而i为3的元素则是原来i为4的元素(数组元素被删除后，后面的元素将会往前移动)
                                 *  这将会导致i为2的元素被跳过，所以将i--
                                 */
                                i--;
                                break;
                            }
                        }
                    }

                    // 不等于0表示并不是原来的数组，进行更新操作
                    if (roles.length != 0) {
                        flag = true;
                    }
                }

                if (flag) {
                    this.putRequest(url).then(resp => {
                        if (resp) {
                            this.initHrs();
                        }
                    });
                }
            },
            showPop(hr) {
                this.initRoles();
                let roles = hr.roles;
                this.selectedRoles = [];
                // 将用户拥有的角色预选中显示出来
                roles.forEach(r=>{
                    this.selectedRoles.push(r.id)
                });
            },
            initHrs() {
                this.getRequest("/system/hr/?keywords=" + this.keywords).then(resp => {
                   if (resp) {
                       this.hrs = resp;
                   }
                });
            },
            changeEnabled(hr) {
                this.putRequest("/system/hr/", hr).then(resp => {
                   if (resp) {
                       this.initHrs();
                   }
                });
            },
            initRoles() {
                this.getRequest("/system/hr/roles").then(resp => {
                   if (resp) {
                       this.allRoles = resp;
                   }
                });
            }
        }
    }
</script>

<style>
    .hr-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 10px;

    }

    .hr-Card {
        width: 350px;
        margin-bottom: 30px;
    }

    .userface-img {
        width: 72px;
        height: 72px;
        border-radius: 72px;
    }

    .img-container {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .userinfo-container {
        margin-top: 20px;
    }

    .userinfo-container div {
        font-size: 12px;
        color: #409eff;
    }
</style>