<template>
    <div>
        <div>
            <el-input
                    size="small"
                    class="addPosInput"
                    placeholder="添加职位..."
                    prefix-icon="el-icon-plus"
                    @keydown.enter.native="addPosition"
                    v-model="pos.name">
            </el-input>
            <el-button icon="el-icon-plus" type="primary" size="small" @click="addPosition">添加职位</el-button>
        </div>
        <div class="posMannMain">
            <el-table
                    :data="positions"
                    size="small"
                    stripe
                    border
                    @selection-change="handleSelectionChange"
                    style="width: 70%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职位名称"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        width="200"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        width="120"
                        label="是否启用">
                    <template slot-scope="scope">
                        <el-tag type="success" size="small" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" size="small" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditWindow(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" size="small" style="margin-top: 8px;" :disabled="multipleSelection.length==0" @click="batchDelPos">批量删除</el-button>
            <el-dialog
                title="修改职位"
                :visible.sync="dialogVisible"
                width="30%">
                <div>
                    <div>
                        <el-tag size="samll" style="margin: 0px 10px 10px 0px;">职位名称</el-tag>
                        <el-input type="text" size="small" class="updateInput" v-model="updatePos.name" style="width: 200px;"></el-input>
                    </div>
                    <div>
                        <el-tag size="small">是否启用</el-tag>
                        <el-switch
                                style="margin-left: 22px;"
                                v-model="updatePos.enabled"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                active-text="启用"
                                inactive-text="禁用">
                        </el-switch>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取 消</el-button>
                    <el-button size="small" type="primary" @click="doUpdatePos">确 定</el-button>
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PosMana",
        data() {
            return {
                pos: {
                    name: '',
                    enabled: false
                },
                dialogVisible: false,
                updatePos: {
                    name: '',
                    enabled: false
                },
                multipleSelection: [],
                positions: []
            }
        },
        mounted() {
          this.initPositions();
        },
        methods: {
            batchDelPos() {
                this.$confirm('此操作将永久删除该【'+ this.multipleSelection.length +'】条数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?ids=';
                    this.multipleSelection.forEach(pos => {
                        ids += pos.id + "&ids=";
                    })

                    this.deleteRequest("/system/basic/pos/" + ids).then(resp => {
                        if (resp) {
                            this.initPositions();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },

            initPositions() {
               this.getRequest('/system/basic/pos/').then(resp => {
                   this.positions = resp;
               });
            },
            addPosition() {
                if (this.pos.name) {
                    this.postRequest("/system/basic/pos/", this.pos).then(resp => {
                        this.initPositions();
                        this.pos.name = '';
                    });
                } else {
                    this.$message.error("职位名称不可以为空");
                }
            },
            doUpdatePos() {
                this.putRequest("/system/basic/pos/", this.updatePos).then(resp => {
                   if (resp) {
                       this.initPositions();
                       this.updatePos.name = '';
                       this.dialogVisible = false;
                   }
                });
            },

            showEditWindow(index, data) {
                Object.assign(this.updatePos, data);
                this.dialogVisible = true;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },

            handleDelete(index, data) {
                this.$confirm('此操作将永久删除【'+ data.name +'】该职位, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/pos/" + data.id).then(resp => {
                        if (resp) {
                            this.initPositions();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        }
    }
</script>

<style>
    .addPosInput {
        width: 300px;
        margin-right: 8px;
    }

    .posMannMain {
        margin-top: 10px;
    }

    .updateInput {
        width: 300px;
        margin-left: 8px;
    }
</style>